import "./key";
import './App.css';
import React, { useState } from 'react';
import Axios from "axios";

function App() {
  const [query, setQuery] = useState(""); // query should be a string initially
  const [recipes, setRecipes] = useState([]); // for storing the recipes
  const [isLoading, setIsLoading] = useState(false); // to show loading status
  const [error, setError] = useState(null); // to show any errors

  const API_KEY = "3beb197c9690416e8301673e7a94b78d";
  var url = `https://api.spoonacular.com/recipes/complexSearch?apiKey=${API_KEY}&query=${query}`;

  async function getRecipes() {
    setIsLoading(true);
    setError(null);
    try {
      const result = await Axios.get(url);
      setRecipes(result.data.results); // assuming the API returns an array of recipes in 'results'
    } catch (error) {
      setError('Failed to fetch recipes');
      console.error('Error fetching recipes', error);
    } finally {
      setIsLoading(false);
    }
  }

  const onSubmit = (e) => {
    e.preventDefault(); // This will prevent a page reload
    getRecipes();
  }

  return (
    <div className="app">
      <h1>Recipe Website</h1>
      <form className="app__searchForm" onSubmit={onSubmit}>
        <input 
          type="text" 
          className="app__input"
          placeholder="Enter ingredient" 
          value={query} 
          onChange={(e) => setQuery(e.target.value)}
        />
        <input className="app__submit" type="submit" value="Search"/>
      </form>    
      {isLoading && <p>Loading...</p>}
      {error && <p>{error}</p>}
      <div className="app__recipes">
        {recipes.map(recipe => (
          <div key={recipe.id}>
            <h3>{recipe.title}</h3>
            <img src={recipe.image} alt={recipe.title} style={{ width: '100px' }} />
          </div>
        ))}
      </div>
    </div>
  );
}

export default App;